import React, { useState } from "react";

import {
  Dialog,
  DialogContent,
  Card,
  CardActionArea,
  CardHeader,
  CardContent,
  CardMedia,
  Typography,
  Avatar,
  CardActions,
  Button,
  Divider,
  DialogTitle,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

import Slide from "@material-ui/core/Slide";
import Typewriter from "typewriter-effect";
import "./App.css";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    "& > *": {
      margin: theme.spacing(0.5),
    },
  },
  small: {
    width: theme.spacing(3),
    height: theme.spacing(3),
  },
}));

export default function HatchDialog({
  open,
  handleClose,
  hatch,
  imgHeight,
  width,
}) {
  const classes = useStyles();
  const [language, setLanguage] = useState("EN");
  const [selected, setSelected] = useState("");
  const [langAnimation, setLangAnimation] = useState("");
  const youtube = hatch.video.includes("https://www.youtube.com/");
  // const videoComponent = loadComponent(hatch.video)

  const handleLanguageAnimation = (lang) => {
    setLangAnimation(lang);
  };

  const handleLanguageChange = (lang) => {
    setLanguage(lang);
    if (lang === "DE") {
      setSelected(lang);
    } else {
      setSelected("");
    }
  };

  const renderVideo = () => {
    if (youtube) {
      return (
        <div className="iframe-container">
          <iframe
            width="max-content"
            height="500px"
            src={hatch.video}
            title={hatch.id}
            frameborder="0"
            gesture="media"
            allow="encrypted-media"
            webkitallowfullscreen
            mozallowfullscreen
            allowFullScreen
          ></iframe>
        </div>
      );
    } else {
      return (
        <DialogContent style={{ backgroundColor: "black" }}>
          <video width="100%" height={500} controls>
            <source
              // src={require(`${hatch.video}?autoplay=false`)}
              src={`${hatch.video}?autoplay=false`}
              type="video/mp4"
            />
          </video>
        </DialogContent>
      );
    }
  };

  const renderText = () => {
    return (
      <Card>
        {hatch.title_en !== "" ? (
          <CardHeader
            title={language === "EN" ? hatch.title_en : hatch.title_de}
            action={
              <div className={classes.root} style={{ paddingTop: "10px" }}>
                <Avatar
                  key="EN"
                  onClick={() => {
                    handleLanguageChange("EN");
                  }}
                  style={{
                    backgroundColor: selected === "DE" ? "" : "#00B32C",
                    height: "35px",
                    width: "35px",
                  }}
                  variant="rounded"
                  sizes="small"
                >
                  EN
                </Avatar>

                <Avatar
                  key="DE"
                  onClick={() => {
                    handleLanguageChange("DE");
                  }}
                  style={{
                    backgroundColor: selected === "DE" ? "#00B32C" : "",
                    height: "35px",
                    width: "35px",
                  }}
                  variant="rounded"
                  sizes="small"
                >
                  DE
                </Avatar>
              </div>
            }
          />
        ) : null}
        <CardActionArea>
          <CardMedia
            style={{ height: imgHeight }}
            image={language === "EN" ? hatch.bibleImg_en : hatch.bibleImg_de}
          >
            {/* <div className="bibleContent">{hatch.bibleContent_en}</div>
            <div className="bibleVerse">{hatch.bibleVerse_en}</div> */}
          </CardMedia>
          {/* <CardMedia>
            <img
              src={hatch.bibleImg}
              alt="Snow"
              height={imgHeight}
              width={500}
            />
          </CardMedia> */}
          <CardContent>
            <Typography
              variant={width === 250 ? "body2" : "h6"}
              color="textSecondary"
              // component={width === 250 ? "p" : "h6"}
              paragraph={true}
            >
              {language === "EN" ? hatch.devotion_en : hatch.devotion_de}
            </Typography>
            <Typography
              variant={width === 250 ? "body2" : "h6"}
              color="textSecondary"
              // component={width === 250 ? "p" : "h6"}
            >
              {language === "EN" ? hatch.summary_en : hatch.summary_de}
            </Typography>
          </CardContent>
        </CardActionArea>

        <CardActions>
          <Divider />
          <Button
            size="small"
            color="primary"
            variant="outlined"
            onClick={handleClose}
          >
            {language === "EN" ? "close" : "schließen"}
          </Button>
        </CardActions>
      </Card>
    );
  };

  const renderMessage = () => {
    if (langAnimation === "EN") {
      return (
        <React.Fragment>
          <Typewriter
            onInit={(typewriter) => {
              typewriter
                .typeString("Hello People of God,")
                .pauseFor(2000)
                .typeString("<br>Here is a message for you<br><br>")
                .pauseFor(2000)
                .typeString(hatch.textAnimation_en)
                .typeString(`<br> ${hatch.bibleVerse_en}`)
                .pauseFor(2000)
                .typeString("<br> <br>Love,")
                .pauseFor(1000)
                .typeString("<br>God")
                .pauseFor(3000)
                .callFunction(() => {
                  handleClose();
                })
                .start();
            }}
          />
        </React.Fragment>
      );
    } else if (langAnimation === "DE") {
      console.log(langAnimation);
      return (
        <Typewriter
          onInit={(typewriter) => {
            typewriter
              .typeString("Hallo Volk Gottes,")
              .pauseFor(2000)
              .typeString("<br>Hier ist eine Nachricht für Sie<br><br>")
              .pauseFor(2000)
              .typeString(hatch.textAnimation_de)
              .typeString(`<br> ${hatch.bibleVerse_de}`)
              .pauseFor(2000)
              .typeString("<br> <br>Liebe,")
              .pauseFor(1000)
              .typeString("<br>Gott")
              .pauseFor(3000)
              .callFunction(() => {
                handleClose();
              })
              .start();
          }}
        />
      );
    } else {
      return "";
    }
  };

  const renderAvatarAnimation = () => {
    if (langAnimation === "") {
      return (
        <React.Fragment>
          <Avatar
            key="EN"
            onClick={() => {
              handleLanguageAnimation("EN");
            }}
            style={{
              backgroundColor: langAnimation === "EN" ? "#00B32C" : "",
            }}
            variant="rounded"
            sizes="small"
          >
            EN
          </Avatar>

          <Avatar
            key="DE"
            onClick={() => {
              handleLanguageAnimation("DE");
            }}
            style={{
              backgroundColor: langAnimation === "DE" ? "#00B32C" : "",
            }}
            variant="rounded"
            sizes="small"
          >
            DE
          </Avatar>
        </React.Fragment>
      );
    } else if (langAnimation === "EN") {
      return (
        <Avatar
          key="EN"
          onClick={() => {
            handleLanguageAnimation("EN");
          }}
          style={{
            backgroundColor: langAnimation === "EN" ? "#00B32C" : "",
          }}
          variant="rounded"
          sizes="small"
        >
          EN
        </Avatar>
      );
    } else {
      return (
        <Avatar
          key="DE"
          onClick={() => {
            handleLanguageAnimation("DE");
          }}
          style={{
            backgroundColor: langAnimation === "DE" ? "#00B32C" : "",
          }}
          variant="rounded"
          sizes="small"
        >
          DE
        </Avatar>
      );
    }
  };
  const renderTextAnimation = () => {
    return (
      <React.Fragment>
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
          <Typography>How do you want to preview the message?</Typography>{" "}
          <Typography>
            Wie möchten Sie eine Vorschau der Nachricht anzeigen?
          </Typography>
          <div className={classes.root} style={{ paddingTop: "10px" }}>
            {renderAvatarAnimation()}
          </div>
        </DialogTitle>
        <DialogContent dividers>
          <div className="text">{renderMessage()}</div>
        </DialogContent>
        {/* <DialogActions>
          <Button
            size="small"
            color="primary"
            variant="outlined"
            onClick={handleClose}
          >
            {langAnimation === "EN" ? "close" : "schließen"}
          </Button>
        </DialogActions> */}
      </React.Fragment>
    );
  };

  return (
    <div>
      <Dialog
        open={hatch.wobble === 0 && open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
        scroll="body"
        maxWidth="sm"
      >
        {hatch.type === "devotion"
          ? renderText()
          : hatch.type === "animation"
          ? renderTextAnimation()
          : renderVideo()}

        {/* {youtube && hatch.wobble === 0 ? (
          <div className="iframe-container">
            <iframe
              width="max-content"
              height="500px"
              src={hatch.video}
              title={hatch.id}
              frameborder="0"
              gesture="media"
              allow="encrypted-media"
              webkitallowfullscreen
              mozallowfullscreen
              allowFullScreen
            ></iframe>
          </div>
        ) : (
          <DialogContent style={{ backgroundColor: "black" }}>
            <video width="100%" height={500} controls>
              <source
                src={require(`${hatch.video}?autoplay=false`)}
                type="video/mp4"
              />
            </video>
          </DialogContent>
        )} */}
      </Dialog>
    </div>
  );
}
