import React, { useState, useEffect, useRef } from "react";

import { Avatar, Grid } from "@material-ui/core";
import { createCalendar } from "./helpers";

import Logo from "./img/LCZ_Logo.png";
import "./App.css";
import HatchDialog from "./HatchDialog";
import TextCard from "./TextCard";
import AudioCard from "./AudioCard";

import PictureCard from "./PictureCard";
import SnowFlakes from "./components/SnowFlakes";
import ErrorDialog from "./ErrorDialog";

function App() {
  const [hatches, setHatches] = useState([]);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [dialogErrorOpen, setDialogErrorOpen] = useState(false);
  const [selected, setSelected] = useState({});
  const [done, setDone] = useState(false);
  const today = new Date();
  const special = new Date(2020, 11, 25);
  const ref = useRef(null);

  useEffect(() => {
    const calendar = localStorage.calendar
      ? JSON.parse(localStorage.calendar)
      : createCalendar();

    setHatches(calendar);
  }, []);

  // Store calendar in localStorage
  useEffect(() => {
    // hatches.length && localStorage.setItem("calendar", JSON.stringify(hatches));
  }, [hatches]);

  // Get the innerWidth of the window, to set the Grid composition (in smaller screens, the scroller gets pushed down)
  function useWindowDimensions() {
    const [windowDimensions, setWindowDimensions] = useState(
      getWindowDimensions()
    );

    useEffect(() => {
      function handleResize() {
        setWindowDimensions(getWindowDimensions());
      }

      window.addEventListener("resize", handleResize);
      return () => window.removeEventListener("resize", handleResize);
    }, []);

    return windowDimensions;
  }
  function getWindowDimensions() {
    const { innerWidth: width, innerHeight: height } = window;
    return { width, height };
  }

  const { width } = useWindowDimensions();

  const handleDialogOpen = (selected) => {
    //remember change this!!!! (month to 11)
    const dateSelected = new Date(2020, 10, selected.nr);

    if (dateSelected <= today) {
      if (
        selected.type === "devotion" ||
        selected.type === "animation" ||
        selected.type === "video"
      ) {
        setDialogOpen(true);
      }
      setSelected(selected);

      const updatedHatches = hatches.map((hatch) =>
        hatch.id === selected.id ? { ...hatch, open: true, wobble: 1 } : hatch
      );

      setHatches(updatedHatches);
    } else {
      setDialogErrorOpen(true);
      setSelected(selected);
    }
  };

  const handleDialogClose = () => {
    setDialogOpen(false);
  };

  const handleErrorDialogClose = () => {
    setDialogErrorOpen(false);
  };

  const handleAnimation = (selected) => {
    setDone(false);
    const updatedHatches = hatches.map((hatch) =>
      hatch.id === selected.id ? { ...hatch, open: true, wobble: 0 } : hatch
    );

    setHatches(updatedHatches);

    if (
      selected.type === "devotion" ||
      selected.type === "animation" ||
      selected.type === "video"
    ) {
      setDone(true);
    }
  };

  const renderItem = (hatch) => {
    if (hatch.visible === true) {
      if (hatch.open && hatch.wobble === 0 && hatch.type === "devotion") {
        return (
          <TextCard
            width={width <= 767 ? 250 : 400}
            height={width <= 767 ? 250 : 400}
            imgHeight={width <= 767 ? 150 : 200}
            backgroundHeight={width <= 767 ? 250 : 400}
            hatch={hatch}
          />
        );
      } else if (hatch.open && hatch.wobble === 0 && hatch.type === "audio") {
        return (
          <AudioCard
            width={width <= 767 ? 250 : 400}
            height={width <= 767 ? 250 : 400}
            imgHeight={width <= 767 ? 100 : 200}
            hatch={hatch}
          />
        );
      } else if (hatch.open && hatch.wobble === 0 && hatch.type === "video") {
        return (
          <TextCard
            width={width <= 767 ? 250 : 400}
            height={width <= 767 ? 250 : 400}
            imgHeight={width <= 767 ? 150 : 200}
            hatch={hatch}
          />
        );
      } else if (hatch.open && hatch.wobble === 0 && hatch.type === "pict") {
        return (
          <PictureCard
            width={width <= 767 ? 250 : 400}
            height={width <= 767 ? 250 : 400}
            hatch={hatch}
          />
        );
      } else if (
        hatch.open &&
        hatch.wobble === 0 &&
        hatch.type === "animation"
      ) {
        return (
          <TextCard
            width={width <= 767 ? 250 : 400}
            height={width <= 767 ? 250 : 400}
            imgHeight={width <= 767 ? 150 : 200}
            backgroundHeight={width <= 767 ? 250 : 400}
            hatch={hatch}
          />
        );
      } else {
        return (
          <React.Fragment>
            <div className="item">
              <Avatar>{hatch.nr}</Avatar>
            </div>

            <div ref={ref}>
              <img
                src={hatch.img}
                alt={hatch.title}
                width={400}
                className="itemImage"
              />
            </div>
          </React.Fragment>
        );
      }
    } else if (today.getTime() === special.getTime()) {
      hatches[24].visible = true;
      setHatches(hatches);
    } else {
      return null;
    }
  };

  return (
    <>
      <SnowFlakes />
      <div className="over" style={{ paddingTop: "10px" }}>
        <img src={Logo} alt="LCZ Logo" className="logoImage" />
      </div>

      <Grid
        container
        direction="row"
        style={{ paddingTop: "20px" }}
        spacing={2}
        alignItems="center"
        justify="center"
      >
        {hatches.map((hatch) => {
          return (
            <Grid
              item
              key={hatch.id}
              onClick={() => {
                handleDialogOpen(hatch);
              }}
              onAnimationEnd={() => handleAnimation(hatch)}
              wobble={hatch.wobble}
            >
              {renderItem(hatch)}
              {/* {hatch.nr === 25 && today === special ? (
                <React.Fragment>
                  <div className="item">
                    <Avatar>{hatch.nr}</Avatar>
                  </div>

                  <div ref={ref}>
                    <img
                      src={hatch.img}
                      alt={hatch.title}
                      width={400}
                      className="itemImage"
                    />
                  </div>
                </React.Fragment>
              ) : null} */}
              {/* {hatch.open && hatch.wobble === 0 && hatch.type === "devotion" ? (
                <TextCard
                  width={width <= 767 ? 250 : 400}
                  height={width <= 767 ? 250 : 400}
                  imgHeight={width <= 767 ? 150 : 200}
                  backgroundHeight={width <= 767 ? 250 : 400}
                  hatch={hatch}
                />
              ) : hatch.open && hatch.wobble === 0 && hatch.type === "audio" ? (
                <AudioCard
                  width={width <= 767 ? 250 : 400}
                  height={width <= 767 ? 250 : 400}
                  imgHeight={width <= 767 ? 100 : 200}
                  hatch={hatch}
                />
              ) : hatch.open && hatch.wobble === 0 && hatch.type === "video" ? (
                <TextCard
                  width={width <= 767 ? 250 : 400}
                  height={width <= 767 ? 250 : 400}
                  imgHeight={width <= 767 ? 150 : 200}
                  hatch={hatch}
                />
              ) : hatch.open && hatch.wobble === 0 && hatch.type === "pict" ? (
                <PictureCard
                  width={width <= 767 ? 250 : 400}
                  height={width <= 767 ? 250 : 400}
                  hatch={hatch}
                />
              ) : hatch.open &&
                hatch.wobble === 0 &&
                hatch.type === "animation" ? (
                <TextCard
                  width={width <= 767 ? 250 : 400}
                  height={width <= 767 ? 250 : 400}
                  imgHeight={width <= 767 ? 150 : 200}
                  backgroundHeight={width <= 767 ? 250 : 400}
                  hatch={hatch}
                />
              ) : (
                <React.Fragment>
                  <div className="item">
                    <Avatar>{hatch.nr}</Avatar>
                  </div>

                  <div ref={ref}>
                    <img
                      src={hatch.img}
                      alt={hatch.title}
                      width={400}
                      className="itemImage"
                    />
                  </div>
                </React.Fragment>
              )} */}
            </Grid>
          );
        })}
      </Grid>

      {done && dialogOpen ? (
        <HatchDialog
          open={dialogOpen}
          hatch={selected}
          handleClose={handleDialogClose}
          width={width <= 767 ? 250 : 400}
          imgHeight={width <= 767 ? 180 : 300}
        />
      ) : null}

      {dialogErrorOpen ? (
        <ErrorDialog
          openError={dialogErrorOpen}
          handleErrorDialogClose={handleErrorDialogClose}
          selected={selected}
        />
      ) : null}
    </>
  );
}

export default App;
