import React from "react";

import {
  Card,
  CardActionArea,
  CardActions,
  CardContent,
  CardMedia,
  Typography,
} from "@material-ui/core";
import test1 from "./audio/test1.mp3";

export default function AudioCard({ width, height, imgHeight, hatch }) {
  const small = width === 250;

  return (
    <Card style={{ width: width, height: height }}>
      <CardActionArea>
        <CardMedia style={{ height: imgHeight }} image={hatch.img} />
        <CardContent>
          <Typography variant={small ? "body2" : "h5"}>
            {hatch.title}
          </Typography>
          <Typography
            variant={small ? "subtitle2" : "subtitle1"}
            color="textSecondary"
          >
            {hatch.singer}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions style={{ paddingLeft: small ? "10px" : "50px" }}>
        <audio controls>
          <source
            // src={require(`${hatch.audio}`)}
            src={test1}
            type="audio/mpeg"
          />
        </audio>
        {/* <div>
          <IconButton aria-label="play/pause">
            <PlayArrowIcon />
          </IconButton>
        </div> */}
      </CardActions>
    </Card>
  );
}
